.row {
  margin-left: 20px;
  color: white;
}
.posters {
  display: flex;
  padding: 10px;
  overflow-x: scroll;
  overflow-y: hidden;
}
.posters::-webkit-scrollbar {
  display: none;
}
.poster {
  max-height: 200px;
  margin-right: 10px;
  cursor: pointer;
}
.poster:hover{ transform: scale(1.5);
  cursor: pointer;}
.smallposter {
  max-height: 100px;
  margin-right: 10px;
  cursor: pointer;
}
.smallposter:hover{ transform: scale(1.8);
  cursor: pointer;}
