.banner {
  background-size: cover;
  height: 448px;
  color: rgb(213, 221, 226);
  overflow: hidden;
  background-color: cadetblue;
}
.content {
  padding-top: 140px;
  height: 190px;
  padding-left: 15px;
}
.titile {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.button {
  color: white;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 5px;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 0.5rem;
  padding-bottom: 00.5rem;
  background-color: rgba(51, 51, 0.5, 0.5);
  cursor: pointer;
  margin-right: 1rem;
}
.button:hover {
  color: black;
  background-color: #e6e6e6;
}
.discription {
  width: 45rem;
  line-height: 1.3rem;
  padding-top: 1rem;
  font-size: 1rem;
  height: 180px;
  max-width: 360px;
  overflow-x: hidden;
  overflow-y: scroll;
}
.discription::-webkit-scrollbar {
  display: none;
}

.fadebottom {
  height: 16.2rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
}
